var toValString = require('././toString')

/**
  * , project-name  projectName
  *
  * @param {String} str 
  * @return {String}
  */
function camelCase (str) {
  return toValString(str).replace(/(-[a-zA-Z])/g, function (text, u) {
    return u.substring(1).toLocaleUpperCase()
  })
}

module.exports = camelCase
