var toValString = require('./toString')

/**
  * , projectName  project-name
  *
  * @param {String} str 
  * @return {String}
  */
function kebabCase (str) {
  return toValString(str).replace(/([A-Z])/g, function (text, u) {
    return '-' + u.toLowerCase()
  })
}

module.exports = kebabCase
