var sum = require('./sum')

var toNumber = require('../number/toNumber')

var getSize = require('../base/getSize')

/**
  * 
  *
  * @param {Array} array 
  * @param {Function/String} iterate 
  * @param {Object} context 
  * @return {Number}
  */
function mean (array, iterate, context) {
  return toNumber(sum(array, iterate, context) / getSize(array))
}

module.exports = mean
